import BigNumber from 'bignumber.js'
import ethereumAddress from 'ethereum-address'
import {getConnection} from './connection'
import UserError from './user-error'
import * as ErrorCodes from './error-codes'
import validateResponseStatus from './utils/validate-response-status'

export default {
  init,
  isInitialized,
  getClientAddress,
  checkBalances,
  isTransacting,
  activeTransactionFinishedPromise,
  transferTokensTo,
  activateContract,
  scoreWork,
  finalizeContract,
}

const RESOLVED_PROMISE = new Promise(resolve => resolve())

let isInitializing = false
let isInitialized = false
let tokenAddress
let internalApi

async function init(tokenContractAddress, expectedNetworkId = 4, internalApiAddress = null) {
  if (isInitializing || isInitialized) {
    throw new UserError(`Already initialized`, ErrorCodes.ALREADY_INITIALIZED)
  }
  try {
    isInitializing = true
    const clientAddress = await _init(tokenContractAddress, expectedNetworkId, internalApiAddress)
    isInitialized = true
    return clientAddress
  } finally {
    isInitializing = false
  }
}

async function _init(tokenContractAddress, expectedNetworkId, internalApiAddress) {
  if (!ethereumAddress.isAddress(tokenContractAddress)) {
    throw new UserError(
      `Invalid Ethereum address: ${tokenContractAddress}`,
      ErrorCodes.INVALID_ETHEREUM_ADDRESS
    )
  }

  tokenAddress = tokenContractAddress
  internalApi = internalApiAddress

  const {web3, networkId, account} = await getConnection()

  if (!networkId) {
    throw new UserError(`no Ethereum client found`, ErrorCodes.NO_ETHEREUM_CLIENT)
  }

  if (!account) {
    throw new UserError(`Ethereum client has no accounts set`, ErrorCodes.NO_ACCOUNTS)
  }

  if (+expectedNetworkId !== +networkId) {
    throw new UserError(
      `The active Ethereum network differs from the expected one`,
      ErrorCodes.WRONG_NETWORK,
    )
  }

  isInitialized = true
  return account
}

function isInitialized() {
  return isInitializing || isInitialized
}

function assertInitialized() {
  if (!isInitialized) {
    throw new UserError(`Not initialized`, ErrorCodes.NOT_INITIALIZED)
  }
}

async function getClientAddress() {
  assertInitialized()
  const {account} = await getConnection()
  return account
}

async function checkBalances(address) {
  assertInitialized()
  const {account} = await getConnection()
  const {ether, token} = await _checkBalances(internalApi, account)
  return {ether, token}
}

let runningPromise

function isTransacting() {
  assertInitialized()
  return !!runningPromise
}

function activeTransactionFinishedPromise() {
  assertInitialized()
  return runningPromise || RESOLVED_PROMISE
}

async function transferTokensTo(address, amount) {
  assertInitialized()
  const {account} = await getConnection()
  return lock(post('_transferTokens', {from: account, to: address, amount}))
}

async function activateContract(contractAddress) {
  assertInitialized()
  const {account} = await getConnection()
  return lock(post('_activateContract', {actorAddress: account, contractAddress}))
}

async function scoreWork(contractAddress, workers) {
  assertInitialized()
  const {account} = await getConnection()
  return lock(post('_scoreWork', {actorAddress: account, contractAddress, workers}))
}

async function finalizeContract(contractAddress) {
  assertInitialized()
  const {account} = await getConnection()
  return lock(post('_finalizeContract', {actorAddress: account, contractAddress}))
}

async function _checkBalances(api, account) {
  const res = await fetch(`${api}/check-balances/${account}`)
  const result = await res.json()
  return result
}

async function post(endpoint, payload) {
  const res = await fetch(`${internalApi}/${endpoint}`, {
    body: JSON.stringify(payload),
    headers: new Headers({
      'Accept': 'application/json',
      'Content-Type': 'application/json',
    }),
    method: 'POST'
  })
  await validateResponseStatus(res)
  const result = await res.json()
  return result
}

async function lock(promise) {
  if (runningPromise) {
    throw new UserError('couldn\'t run two parallel transactions', ErrorCodes.TRANSACTION_ALREADY_RUNNING)
  }

  runningPromise = promise.then(() => undefined, () => undefined)

  try {
    const res = await promise
    return res
  }
  finally {
    runningPromise = null
  }
}



